<template>
	<view class="home-box">
		
			
			<u-swiper :list="imagelist"  duration="3000" height="300"></u-swiper>
			
			
			
			<view v-for="(item,index) in shoplist" :key="index">
				<view style="margin: 15px 0;font-size: 16px;font-weight: 700;">{{item.name}}</view>
				<view class="textbox">
					<view class="content" v-html="item.content"></view>
				</view>
				<view class="shopitem" >
					<image v-for="(itemX,indexX) in item.image.split(',')" :key="itemX,indexX" :src="itemX" mode="aspectFill"></image>
					
				</view>
				
			</view>
			
	</view>
</template>
<script>
	
	export default {
	
		data() {
			return {
				typeID:'',
				typeDetail:[],
				tabBarIndex:0,
				scrollIntoIndex:'top0',
				// 内容高度
				contentHeight:0,
				tabBar:[],
				goodsData:[],
				yearNum:0,
				show:false,
				imagelist:[],
				shoplist:{}
				
			}
		},
		onLoad(){
			this.getTypeData()
			this.getGoodsData()
			console.log(this.$store.state.loginStatus,'loginStatus');
			// this.SocketOnline()
			this.getimage()
			this.getshop()
			
			
		},
		onShow() {
			// this.SocketOnline()
			// this.$store.dispatch('openSoket')
		},
		onReady(){
			// 获取高度
			
			let view = uni.createSelectorQuery().select('.home-element');
			view.boundingClientRect(data =>{
				console.log(data.height)
				this.contentHeight = data.height + 1400;
			}).exec();
			
			
		},
		methods: {
			toSearch(){
				uni.navigateTo({
					url:'../searchpage/searchpage'
				})
			},
			confirm(e) {
				console.log(e[0].value,'eeeeeeee');
				this.yearNum = e[0].value;
				this.yearName = e[0].label;
				this.getGoodsData()
			},
			// 获取分类数据
			getTypeData(){
				uni.request({
					url:this.$elyasApi+'goods/type/findByModal',
					method:'POST',
					data:{},
					header:{
						"content-type":"application/json"
					},
					success:(data)=> {
						this.tabBar = data.data.data.list;
						this.tabBar.unshift({goodsType:'推荐',index:0});
					}
					
				});
			},
			getshop(){
				uni.request({
					url:this.$elyasApi+'shop/findByModal',
					method:'POST',
					data:{},
					header:{
						"content-type":"application/json"
					},
					success:(data)=> {
						this.shoplist = data.data.data.list;
						
					}
					
				});
			},
			// 获取分类详情
			getTypeDetail(){
				uni.request({
					url:this.$elyasApi+'goods/type/detail?id='+this.typeID,
					method:'POST',
					data:{},
					header:{
						"content-type":"application/json"
					},
					success:(data)=> {
						this.typeDetail = data.data.data.goodsList;
					}
					
				});
			},
			// 获取公开课列表
			getGoodsData(){
				uni.request({
					url:this.$elyasApi+'goods/findByModal',
					method:'POST',
					data:{},
					header:{
						"content-type":"application/json"
					},
					success:(data)=> {
						this.goodsData = data.data.data.list;
					}
					
				});
			},
			getimage(){
				uni.request({
					url:this.$elyasApi+'cover/findByModal',
					method:'POST',
					data:{},
					header:{
						"content-type":"application/json"
					},
					success:(data)=> {
						this.imagelist = data.data.data.list;
					}
					
				});
			},
			toTab(){
				uni.navigateTo({
					url:'../classList/classList'
				})
			},
			toArticlePage(){
				this.checkAuth(()=>{
					uni.navigateTo({
						url:'../article-page/article-page'
					})
				})
				
			},
			// 点击切换tab
			changeIndex(index,itemId){
				if (this.tabBarIndex === index){
					return;
				}
				this.tabBarIndex = index;
				this.scrollIntoIndex = 'top'+index;
				// console.log(itemId,'item id');
				this.typeID = itemId;
				this.getTypeDetail()
			},
			onChangeSwiper(e){
				this.changeIndex(e.detail.current);
				
			},
			 // 免费课程详情
			toGoodsDetail(id){
				uni.navigateTo({
					url:'../goods-detail-buy/goods-detail-buy?id='+id
				})
			},
			// 收费课程详情
			toGoodsDetailBuy(id){
				uni.navigateTo({
					url:'../goods-detail-buy/goods-detail-buy?id='+id
				})
			},
			// 测试 跳转登录页
			toLogin(){
				uni.navigateTo({
					// url:'../login/login'
				})
			},
			test(){
				uni.navigateTo({
					url:'../cart/cart'
				})
			},
			toHelp(){
				uni.navigateTo({
					url:'../help-page/help-page'
				})
			},
			happenTimeFun(num){//时间戳数据处理
						
			let date = new Date(num);
			
			//时间戳为10位需*1000，时间戳为13位的话不需乘1000
			
			        let y = date.getFullYear();
			
			        let MM = date.getMonth() + 1;
			
			        MM = MM < 10 ? ('0' + MM) : MM;//月补0
			
			        let d = date.getDate();
			
			        d = d < 10 ? ('0' + d) : d;//天补0
			
			      
			        return y + '-' + MM + '-' + d;
			
			},
			// socket online
			SocketOnline(){	
				uni.request({
					url:this.$elyasApi+'socket/join?userName='+this.$store.state.userInfo.userName,
					method:'GET',
					data:{},
					header:{
						"content-type":"application/json"
					},
					success:(data)=> {
						console.log(data)
					}
					
				});
			},
			openShopDetail(id){
				uni.request({
					url:this.$elyasApi+'shop/detail?id='+id,
					method:'POST',
					data:{},
					header:{
						"content-type":"application/json"
					},
					success:(data)=> {
						console.log(data)
					}
					
				});
			},
			toDabao(){
				uni.switchTab({
					url:'../buylist/buylist'
				})
				
			}
		}
	}
</script>

<style lang="scss">
	.lineTe{
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 1;
		-webkit-box-orient: vertical;
	}
	.home-box{
		padding: 0 30rpx;
	}
	
	
	
	.buttontwo{
		display: flex;
		align-items: center;
		justify-content: space-between;
		
	}
	
	.buttontwo button {
		flex: 1;
		margin: 10px 0;
		padding: 20px 0;
		color: #fff;
		font-weight: 700;
		font-size: 20px;
	}
	
	.buttontwo button:nth-child(1){
		
		
		
		border-radius: 10px;
		background: linear-gradient(145deg, #f6d365, #fda085);
		box-shadow:  6px 6px 8px #fda085,
		             -6px -6px 8px #f6d365;
	}
	
	.buttonc{
		// background-color: #FBAB7E;
		// background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
border-radius: 10px;
background: linear-gradient(145deg, #F7CE68, #FBAB7E);
box-shadow:  6px 6px 8px #FBAB7E,
             -6px -6px 8px #F7CE68;
	}
	
	.shopitem {
		margin: 10px 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
	}
	
	.shopitem image {
		width: 170px;
		height: 170px;
		border-radius: 10px;
		margin: 1px;
		
	}
	
	.shopitem .textbox {
		flex: 1;
	}
	
	.shopitem .textbox .title{
		font-size: 18px;
		font-weight: 600;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 1;
		-webkit-box-orient: vertical;
	}
	
	
	
	.textbox .content{
		font-size: 12px;
		font-weight: 500;
		color: #999;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 4;
		-webkit-box-orient: vertical;
	}
		
	
	
</style>
